.about {
  padding: 5rem 1rem;
  background-color: #f2f2f2;
}

.about .content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.about .content > img {
  width: 12rem;
}

.about .content .presentation {
  width: 40rem;
  margin-left: 5rem;
}

.about .content .presentation h2 {
  margin: 0;
  font-size: 2.5rem;
}

.about .content .presentation .social svg {
  display: inline-block;
  margin-right: .5rem;
  fill: #1a1a1a;
  -webkit-transition: fill .3s;
  -o-transition: fill .3s;
  transition: fill .3s;
}

.about .content .presentation .social svg:hover {
  fill: #808080;
}



