.currently-reading {
  position: relative;
  width: 18%;
  color: #fff;
  -webkit-transition: width .5s, padding .5s;
  -o-transition: width .5s, padding .5s;
  transition: width .5s, padding .5s;
}

.currently-reading > * {
  padding: .5rem 1rem;
  opacity: 0;
  -webkit-transform: translateY(1rem);
      -ms-transform: translateY(1rem);
          transform: translateY(1rem);
  -webkit-transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, -webkit-transform .5s;
  -o-transition: opacity .5s, transform .5s;
  transition: opacity .5s, transform .5s;
  transition: opacity .5s, transform .5s, -webkit-transform .5s;
}

.currently-reading.appear > * {
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.currently-reading .background {
  position: absolute;
  width: 0;
  height: calc(100% - 1rem);
  -webkit-transition: width .5s;
  -o-transition: width .5s;
  transition: width .5s;
}

.currently-reading.appear .background {
  width: 100%;
}

.currently-reading h1,
.currently-reading .book-title {
  font-size: 2.4rem;
}

.currently-reading .book-author {
  font-size: 1.8rem;
}

.currently-reading .book-title,
.currently-reading .book-author {
  -webkit-transition-delay: .6s;
       -o-transition-delay: .6s;
          transition-delay: .6s;
}

.currently-reading img {
  position: relative;
  width: calc(100% + 2rem);
  left: calc(15% - 1rem);
  -webkit-transition-delay: .3s;
       -o-transition-delay: .3s;
          transition-delay: .3s;
}



