/* Landscape tablets */
@media (max-width: 1024px) {
  .post-summary .content {
    padding-right: 0;
  }

  .post-summary .not-desktop-books {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    margin-top: .5rem;
    height: 6rem;
  }

  .post-summary .not-desktop-books img {
    height: 100%;
    width: auto;
    margin-right: .5rem;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    -webkit-transition: -webkit-filter .3s, -webkit-transform .3s;
    transition: -webkit-filter .3s, -webkit-transform .3s;
    -o-transition: filter .3s, transform .3s;
    transition: filter .3s, transform .3s;
    transition: filter .3s, transform .3s, -webkit-filter .3s, -webkit-transform .3s;
  }

  .post-summary .not-desktop-books img:hover {
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
  }

  .post-summary .content .post-link {
    width: auto;
  }
}

/* Landscape smartphones and portrait tablets */
@media (max-width: 719px) {
  .post-summary .content h1 {
    font-size: 1.4rem;
  }

  .post-summary .not-desktop-books {
    height: 5rem;
  }

  .post-summary .not-desktop-books img {
    margin-right: .2rem;
  }
}



