.comment-form {
  position: relative;
}

.comment-form h2 {
  margin: 3rem 0 .5rem;
  font-size: 1.6rem;
}

.comment-form form {
  height: 2rem;
  -webkit-transition: height .3s;
  -o-transition: height .3s;
  transition: height .3s;
}

.comment-form form.active {
  height: auto;
}

.comment-form form textarea {
  width: calc(100% - .25rem);
  max-width: calc(100% - .25rem);
  height: 1rem;
  border: 0;
  border-bottom: .25rem solid;
  border-color: transparent transparent #e6e6e6 transparent;
  font-size: 1rem;
  -webkit-transition: height .3s, border-color .3s;
  -o-transition: height .3s, border-color .3s;
  transition: height .3s, border-color .3s;
}

.comment-form form.active textarea {
  height: 7rem;
  width: calc(100% - .5rem);
  max-width: calc(100% - .5rem);
  border: .25rem solid #e6e6e6;
}

.comment-form form textarea:focus {
  border-color: #ccc;
  outline: 0;
}

.comment-form form .contact {
  opacity: 0;
  -webkit-transition: opacity .5s .3s;
  -o-transition: opacity .5s .3s;
  transition: opacity .5s .3s;
}

.comment-form form.active .contact {
  display: -ms-flexbox;
  display: flex;
  margin-top: 1rem;
  opacity: 1;
}

.comment-form form .contact input {
  -ms-flex: 1 1;
      flex: 1 1;
  border: 0;
  border-bottom: .25rem solid #f2f2f2;
  font-size: 1rem;
  -webkit-transition: border-bottom-color .3s;
  -o-transition: border-bottom-color .3s;
  transition: border-bottom-color .3s;
}

.comment-form form .contact input.required {
  border-color: #e6e6e6;
}

.comment-form form .contact input:focus {
  outline: 0;
  border-bottom-color: #ccc;
}

.comment-form form .contact input:not(:last-child) {
  margin-right: 1rem;
}

.comment-form form .submit {
  width: 100%;
  margin-top: 1rem;
  padding: .5rem;
  border: 1px solid #333;
  background-color: #fff;
  opacity: 0;
  color: #333;
  font-size: 1.1rem;
  font-style: italic;
  -webkit-transition: color .3s, background-color .3s, opacity .5s .6s;
  -o-transition: color .3s, background-color .3s, opacity .5s .6s;
  transition: color .3s, background-color .3s, opacity .5s .6s;
}

.comment-form form.active .submit {
  opacity: 1;
}

.comment-form form .submit:hover {
  background-color: #333;
  color: #fff;
  cursor: pointer;
}

.comment-form form .submit:focus {
  outline: 0;
}

.comment-form .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: #fff;
  opacity: .7;
}



