.post-summary {
  margin: 2.5rem 0;
  min-height: 12rem;
  display: -ms-flexbox;
  display: flex;
}

.post-summary .content {
  -ms-flex: 1 1;
      flex: 1 1;
  padding-right: 2rem;
}

.post-summary .content h1 {
  position: relative;
  margin: 0;
  font-size: 2rem;
}

.post-summary .content .date {
  display: block;
  font-size: .8rem;
}

.post-summary .content .post-link {
  display: block;
  width: 100%;
  padding: .5rem;
  border: 1px solid #333;
  text-align: center;
  text-decoration: none;
  color: #333;
  font-style: italic;
  font-size: 1.1rem;
  -webkit-transition: background-color .3s, color .3s;
  -o-transition: background-color .3s, color .3s;
  transition: background-color .3s, color .3s;
}

.post-summary .content .post-link:hover {
  background-color: #333;
  color: #fff;
}

.post-summary .books {
  position: relative;
  width: 20rem;
}

.post-summary .books img {
  position: absolute;
  height: 9rem;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  -webkit-transition: -webkit-filter .3s, -webkit-transform .3s;
  transition: -webkit-filter .3s, -webkit-transform .3s;
  -o-transition: filter .3s, transform .3s;
  transition: filter .3s, transform .3s;
  transition: filter .3s, transform .3s, -webkit-filter .3s, -webkit-transform .3s;
}

.post-summary .books img:hover {
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
  -webkit-transform: translateY(-2rem);
      -ms-transform: translateY(-2rem);
          transform: translateY(-2rem);
}



