.post-navigation > a {
  position: relative;
  display: inline-block;
  margin: 1rem 0 2rem;
  font-size: 1.3rem;
  font-style: italic;
}

.post-navigation > a::before {
  content: '';
  position: absolute;
  width: calc(100% - .1rem);
  height: 1px;
  top: 2rem;
  left: .1rem;
  background-color: #1a1a1a;
  -webkit-transform-origin: left;
      -ms-transform-origin: left;
          transform-origin: left;
  -webkit-transform: scaleX(.2);
      -ms-transform: scaleX(.2);
          transform: scaleX(.2);
  -webkit-transition: background-color .3s, -webkit-transform .3s ease-in-out;
  transition: background-color .3s, -webkit-transform .3s ease-in-out;
  -o-transition: transform .3s ease-in-out, background-color .3s;
  transition: transform .3s ease-in-out, background-color .3s;
  transition: transform .3s ease-in-out, background-color .3s, -webkit-transform .3s ease-in-out;
}

.post-navigation > a:hover::before {
  background-color: #808080;
  -webkit-transform: scaleX(1);
      -ms-transform: scaleX(1);
          transform: scaleX(1);
}

.post-navigation > a::after {
  content: '';
  position: absolute;
  top: 1.75rem;
  left: 0;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: .3rem .6rem .3rem 0;
  border-color: transparent #1a1a1a transparent transparent;
  -webkit-transition: border-color .3s;
  -o-transition: border-color .3s;
  transition: border-color .3s;
}

.post-navigation > a:hover::after {
  border-color: transparent #808080 transparent transparent;
}

.post-navigation footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0 -5rem;
  padding: 2rem 5rem;
  background-color: #f2f2f2;
}



