.spinner-wrapper.hidden {
  display: none;
}

.spinner-wrapper {
  display: block;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: #fff;
}

.spinner {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.spinner > img {
  margin: 0 .5rem;
  -webkit-animation: bounce 1.7s infinite ease-in-out both;
          animation: bounce 1.7s infinite ease-in-out both;
}

.spinner > img:nth-child(1) {
  -webkit-animation-delay: -.6s;
          animation-delay: -.6s;
}

.spinner > img:nth-child(2) {
  -webkit-animation-delay: -.4s;
          animation-delay: -.4s;
}

.spinner > img:nth-child(3) {
  -webkit-animation-delay: -.2s;
          animation-delay: -.2s;
}

@-webkit-keyframes bounce {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
  }
  40% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
    opacity: 1;
  }
}

@keyframes bounce {
  0%, 80%, 100% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
  }
  40% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
    opacity: 1;
  }
}



