@font-face {
  font-family: 'Lora';
  src: url('/static/fonts/Lora-Regular.ttf');
}

@font-face {
  font-family: 'Lora';
  font-style: italic;
  src: url('/static/fonts/Lora-Italic.ttf');
}

@font-face {
  font-family: 'Lora';
  font-weight: bold;
  src: url('/static/fonts/Lora-Bold.ttf');
}

@font-face {
  font-family: 'Lora';
  font-style: italic;
  font-weight: bold;
  src: url('/static/fonts/Lora-BoldItalic.ttf');
}

::-moz-selection {
  background-color: #333;
  color: #fff;
}

::selection {
  background-color: #333;
  color: #fff;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Lora';
  color: #333;
}

button,
textarea,
input {
  font-family: 'Lora';
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  font-style: italic;
}

p {
  font-size: 1.1rem;
  line-height: 1.7rem;
}

img {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

a {
  color: #333;
  text-decoration: none;
  -webkit-transition: color .2s;
  -o-transition: color .2s;
  transition: color .2s;
}

a:hover {
  color: #808080;
}

svg:focus {
  outline: 0;
}



