.post > .content {
  margin: 3rem 5rem;
}

.post > .content > * {
  opacity: 0;
  -webkit-transform: translateY(1rem);
      -ms-transform: translateY(1rem);
          transform: translateY(1rem);
  -webkit-transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, -webkit-transform .5s;
  -o-transition: opacity .5s, transform .5s;
  transition: opacity .5s, transform .5s;
  transition: opacity .5s, transform .5s, -webkit-transform .5s;
}

.post > .content > *:not(header) {
  -webkit-transition-delay: .3s;
       -o-transition-delay: .3s;
          transition-delay: .3s;
}

.post.appear > .content > * {
  opacity: 1;
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.post > .content > header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 3rem;
  padding-bottom: .5rem;
  border-bottom: 1px solid #333;
}

.post > .content > header .title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.post > .content > header .title h1 {
  margin: 0;
  font-size: 2.4rem;
}

.post > .content > header .title .date {
  display: block;
}

.post > .content > header .books {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-align: end;
      align-items: flex-end;
}

.post > .content > header .books img {
  height: 8rem;
  margin-left: .5rem;
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
  -webkit-transition: -webkit-filter .3s;
  transition: -webkit-filter .3s;
  -o-transition: filter .3s;
  transition: filter .3s;
  transition: filter .3s, -webkit-filter .3s;
}

.post.appear > .content > header .books img {
  -webkit-animation: .5s book-appear both;
          animation: .5s book-appear both;
}

.post > .content > header .books img:hover {
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
}

@-webkit-keyframes book-appear {
  from {
    -webkit-animation-timing-function: cubic-bezier(.5, -.5, .5, 1.5);
            animation-timing-function: cubic-bezier(.5, -.5, .5, 1.5);
    -webkit-transform: scale(0) translateY(-2rem);
            transform: scale(0) translateY(-2rem);
  }
  50% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    -webkit-transform: scale(1) translateY(-2rem);
            transform: scale(1) translateY(-2rem);
  }
  to {
    -webkit-transform: scale(1) translateY(0);
            transform: scale(1) translateY(0);
  }
}

@keyframes book-appear {
  from {
    -webkit-animation-timing-function: cubic-bezier(.5, -.5, .5, 1.5);
            animation-timing-function: cubic-bezier(.5, -.5, .5, 1.5);
    -webkit-transform: scale(0) translateY(-2rem);
            transform: scale(0) translateY(-2rem);
  }
  50% {
    -webkit-animation-timing-function: ease;
            animation-timing-function: ease;
    -webkit-transform: scale(1) translateY(-2rem);
            transform: scale(1) translateY(-2rem);
  }
  to {
    -webkit-transform: scale(1) translateY(0);
            transform: scale(1) translateY(0);
  }
}



