.post-comment {
  padding: .5rem 1rem;
  display: -ms-flexbox;
  display: flex;
}

.post-comment:nth-child(even) {
  background-color: #f2f2f2;
}

.post-comment .avatar-and-pseudo {
  text-align: right;
  -ms-flex: 0 0 15rem;
      flex: 0 0 15rem;
  padding-right: 2rem;
}

.post-comment.admin .avatar-and-pseudo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.post-comment .avatar-and-pseudo .pseudo {
  margin: .5rem 0 0 0;
  font-size: 1.2rem;
}

.post-comment.admin .avatar-and-pseudo .pseudo {
  color: #3737c8;
  font-weight: bold;
}

.post-comment .date {
  display: block;
  margin-bottom: .3rem;
  font-size: .8rem;
  color: #808080;
}

.post-comment .avatar-and-pseudo .links svg {
  display: inline;
  margin-left: .2rem;
  fill: #333;
  -webkit-transition: fill .3s;
  -o-transition: fill .3s;
  transition: fill .3s;
}

.post-comment .avatar-and-pseudo .links svg:hover  {
  fill: #808080;
}

.post-comment.admin .avatar-and-pseudo .admin-avatar {
  -ms-flex: 0 0 4rem;
      flex: 0 0 4rem;
  padding-left: .5rem;
}

.post-comment.admin .avatar-and-pseudo .admin-avatar img {
  width: 100%;
}

.post-comment .text * {
  max-width: 100%;
  margin: .5rem 0;
  font-size: 1rem;
  line-height: 1.4rem;
}



